
.slider__img img{
    height: max-content;
    width: max-content;
} 
.certificate__slider{ 
    margin: 0 3rem 0 3rem;
    overflow: hidden;
    position: relative;
    width: auto;
   
    background-color: var(--container-color);
}
.slider__track{
    animation: slide 40s linear infinite;
    display: flex;
    width: calc(250px * 9 );
}
.slider__img{
    height: max-content;
    width: max-content;

}
@keyframes slide{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(calc(-250px * 3));
    }
}

/* Breakpoints */
/* for larger devices */
@media screen and (max-width: 992px) {
    .slider__img img{
        height: 70px;
        width: 120px;
    } 
    .slider__img{
        padding-top: 1rem;
        height: max-content;
        width: max-content;
    }
    .certificate__slider{ 
        margin: 0 2rem 0 2rem;
        
    }
  }

/* for medium devices */
@media screen and (max-width: 768px) {
    .slider__img img{
        height: 70px;
        width: 120px;
    } 
    .slider__img{
        padding-top: 1rem;
        height: max-content;
        width: max-content;
    }
    .certificate__slider{ 
        margin: 0 2rem 0 2rem;
        
    }


  }

/* for small devices */
@media screen and (max-width: 350px) {
    .slider__img img{
        height: 70px;
        width: 120px;
    } 
    .slider__img{
        padding-top: 1rem;
        height: max-content;
        width: max-content;
    }

.certificate__slider{ 
    margin: 2rem;
    padding-bottom: 1rem;

    }
  }

  
  